.img-proyect {
  max-width: 100%;
  width: 300px;
  height: 100px;
  margin-top: -125px;
  margin-left: -300px; /* Cambiado para empezar fuera del contenedor a la izquierda */
  position: absolute;
  border: 1px solid #ddd;
  visibility: hidden;
  transition: margin-left 0.5s ease-in-out, visibility 0.3s ease-in-out; /* Agregada transición para margin-left */
}

.proyect-item:hover .proyect-title-and-description {
  visibility: hidden;
}

.proyect-item:hover .img-proyect {
  visibility: visible;
  margin-left: -150px; /* Cambiado para terminar en la posición original */
}

.proyect-title-and-description {
  width: 300px;
  height: 100px;
  visibility: visible;
}

.proyect-title-and-description:hover {
  visibility: hidden;
}

.proyects-container {
  display: flex;
  flex-direction: column;
  text-align: center;
}

#proyecto1img {
  padding-top: 3px;
  cursor: pointer; /* Hace que el cursor cambie al puntero al pasar sobre la imagen */
}
